import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, ToastController } from 'ionic-angular';
import { Connect } from '../../providers/connect';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

 error: any;
  form: any;
  user: any = '';
  name: any = '';

  constructor(public loadingCtrl: LoadingController, public navCtrl: NavController, public toastCtrl : ToastController, public connect: Connect, public navParams: NavParams) {
    this.form = {
      email: '',
      password: ''
    }
    this.name='';
    this.user='';
  }


  private presentToast(text) {
    let toast = this.toastCtrl.create({
      message: text,
      duration: 2000,
      position: 'top'
    });
    toast.present();
  }

  ionViewDidEnter() {
    this.user=window.localStorage.getItem('userTag');
    if (!this.user) this.user='';
    if (this.user.length) 
      this.name=window.localStorage.getItem('userName');
  }

  login() {
    let loading = this.loadingCtrl.create({
      content: 'Please wait...'
    });
    loading.present();
    let that = this;
    this.connect.getList('nvlogin.php?params='+encodeURI(this.form.email+'^'+this.form.password)).subscribe(
          data => {
            loading.dismiss();
            if (data.success > '0') {
               that.user = data.id;
               that.name = data.name; 
               window.localStorage.setItem('userTag', that.user);
               window.localStorage.setItem('userName', that.name); 
              
            }
            else {
                that.error = data.error; 
            }
          },
          err => this.connect.logError(err)
        );
  }

  dologout()
  { window.localStorage.setItem('userTag','');
    this.user='';
  }

}



